<script>
import TableComponent from "@/components/Table/Table.vue";
import PaginationComponent from "@/components/Pagination1/Pagination1.vue";

//2024.04.18 新增搜尋功能
// 將搜尋內容轉小寫後，篩選符合結果
const toLower = (text) => {
  return text ? text.toString().toLowerCase() : text || "";
};
const searchByName = (items, term) => {
  if (term) {
    return items.filter(
      (item) =>
        toLower(item.prjInfo).includes(toLower(term)) ||
        toLower(item.date).includes(toLower(term))
    );
  }
  return items;
};

export default {
  name: "workOrderDataManagementComponent",
  components: { TableComponent, PaginationComponent },
  data() {
    return {
      tableColumns: [
        {
          id: "index",
          label: "項次",
          width: "70",
        },
        {
          id: "apply_Pk",
          label: "編號",
          width: "140",
        },
        {
          id: "prjInfo",
          label: "工程編號(工程名稱)",
          width: "200",
        },
        {
          id: "date",
          label: "工作日期(申請日期)",
          width: "180",
        },
        {
          id: "status",
          label: "審核狀態",
          width: "150",
        },
        {
          id: "address",
          label: "詳述區域",
          width: "300",
        },
        {
          id: "area_Name",
          label: "工作區",
          width: "300",
        },
        {
          id: "work_Content",
          label: "工作內容",
          width: "300",
        },
      ],
      search: "",
      data: [],
      tableData: [],
      tableList: [],
    };
  },
  computed: {},
  methods: {
    changePage(page) {
      this.$store.state.currentPage = page;
      const cur = (page - 1) * 10;
      const last = page * 10;
      this.tableList = this.tableData.slice(cur, last);
    },
    viewHandler(item) {
      const { href } = this.$router.resolve({
        path: "/engineeringDataManagementView",
        query: {
          data: JSON.stringify(item),
        },
      });
      window.open(href, "_blank");
    },
    printHandler(apply_Pk) {
      window.open(
        `/#/engineeringDataManagementPrint/${encodeURIComponent(apply_Pk)}`
      );
    },
    viewDetailHandler(apply_Pk) {
      window.open(
        `/#/engineeringDataManagementViewDetail/${encodeURIComponent(apply_Pk)}`
      );
    },
    // API取得資料
    async getEveryDayList() {
      try {
        const res = await this.$store.dispatch("getEveryDayList");
        const data = res.data.data;
        this.data = JSON.parse(JSON.stringify(data));
        this.tableData = this.data;
        this.tableData.forEach((e, index) => {
          e.index = index + 1;
          e.prjInfo = `${e.project_No}(${e.project_Name})`;
          e.date = `${e.work_Date}(${e.apply_Date})`;
          e.address = `${e.ld2}/ ${e.ld2e}/ ${e.ld4}/ ${e.ld3}/ ${e.floor}/ ${e.area_Name}`;
          e.project_Status = e.status;
          e.status = this.convertStatus(e.status);
        });
        this.tableList = this.tableData.slice(0, 10);
      } catch (err) {
        console.log(err);
      }
    },
    //2024.04.18 新增搜尋功能
    // 篩選搜尋之內容，並呈現表格第一頁
    async searchInput(evt) {
      if (evt.code === "Enter" || evt.code === "NumpadEnter") {
        const val = evt.target.value;
        this.tableData = searchByName(this.data, val);
        this.tableList = this.tableData.slice(0, 10);
        this.$store.state.currentPage = 1;
      }
    },
  },
  async mounted() {
    this.$store.state.currentPage = 1;
    await this.getEveryDayList();
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;
  },
};
</script>

<template src="./workOrderDataManagement.html"></template>
